import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { IUserPointsVM } from "app/admin/models/admin/gifts";
import { environment } from "environments/environment";
import { Subject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class GiftsService {
  constructor(private http: HttpClient) {}
  userPoints = new Subject<IUserPointsVM>();

  getGiftCategoryByID(id: any) {
    return this.http.get(
      `${environment.apiURL}Admin/get-gift-category-by-id/${id}`
    );
  }

  // getGiftByCategoryID(CategoryID: any) {
  //   return this.http.get(
  //     environment.apiURL + "Admin/getGiftsBank/" + CategoryID
  //   );
  // }

  RemoveGiftCategory(giftCategoryId: number) {
    return this.http.get(
      `${environment.apiURL}Admin/removeGiftCategory/${giftCategoryId}`
    );
  }

  RemoveGift(giftID: any) {
    return this.http.get(environment.apiURL + "Admin/removeGift/" + giftID);
  }

  getCategoriesAll<T>(eduCompId: number, page: number) {
    return this.http.get<T>(
      `${environment.apiURL}Admin/getGiftsCategoriesAll/${eduCompId}/${page}`
    );
  }

  getGiftsAll<T>(eduCompId: number, page: number) {
    return this.http.get<T>(
      `${environment.apiURL}Admin/getGiftsAll/${eduCompId}/${page}`
    );
  }

  getGiftCategoryDropDown<T>(eduCompId: number) {
    return this.http.get<T>(
      `${environment.apiURL}Admin/getAllGiftCategoryDrop/${eduCompId}`
    );
  }

  getCategoriesSearch(page: number, body: any) {
    return this.http.post(
      `${environment.apiURL}Admin/getGiftsCategories/${page}`,
      body
    );
  }

  getGiftsSearch(eduCompId: number, page: number, body: any) {
    return this.http.post(
      `${environment.apiURL}Admin/getGiftsSearch/${eduCompId}/${page}`,
      body
    );
  }

  getCategoryById(Id: any) {
    return this.http.get(
      environment.apiURL + "Admin/get-gift-category-by-id/" + Id
    );
  }

  getGiftGallery() {
    return this.http.get(environment.apiURL + "Admin/getGiftGallery");
  }

  getPoints() {
    return this.http
      .get(environment.apiURL + "Admin/getPointsOfUser")
      .subscribe((res: IUserPointsVM) => {
        this.userPoints.next(res);
      });
  }

  addEditCategory(body: any) {
    return this.http.post(`${environment.apiURL}Admin/category-add-edit`, body);
  }

  addGift(body: any) {
    return this.http.post(`${environment.apiURL}Admin/addNewGift`, body);
  }

  getGiftById(id: any) {
    return this.http.get(`${environment.apiURL}Admin/get-gift-by-Id/${id}`);
  }

  deleteAttach(id: number) {
    return this.http.get(
      `${environment.apiURL}Admin/delete-Attach-By-Id/${id}`
    );
  }

  buyGift(giftId: number) {
    return this.http.get(`${environment.apiURL}Admin/buyGift/${giftId}`);
  }
}
