import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IfileVM } from "app/shared/models/general/general";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-file-input",
  templateUrl: "./file-input.component.html",
  styleUrls: ["./file-input.component.scss"],
})
export class FileInputComponent implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() accept?: string;
  @Input() htmlId?: string;

  constructor(
    private fileService: UploadFileService,
    private msg: MessagesService
  ) {}

  ngOnInit(): void {}

  async uploadFile(event) {
    await this.fileService.uploadFile(event).then((file: IfileVM) => {
      if (file.size > 10485760) {
        // file > 10 .MB
        this.msg.warning("General.largFileError");
        if (this.htmlId) this.fileService.resetFileById(this.htmlId);
        else this.fileService.resetFileInputs();
      } else this.control?.setValue(file);
    });
  }
}
