import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { environment } from 'environments/environment'

@Injectable({
  providedIn: 'root',
})
export class JopTitleDepartmentService {
  constructor(private _http: HttpClient) {}

  getJopTitlebyDepartment<T>(userid: number, page: number) {
    return this._http.get<T>(
      `${environment.apiURL}usermanagement/returnAllUserDepartments/${userid}/${page}`,
    )
  }

  addJopTitlebyDepartment<T>(model: T) {
    return this._http.post<T>(
      `${environment.apiURL}usermanagement/CreateUserDepartmentJobTitle`,
      model,
    )
  }

  editAllDeparment<T>(model: T, id: number) {
    return this._http.put<T>(
      `${environment.apiURL}usermanagement/EditUserDepartmentJobTitle/${id}`,
      model,
    )
  }

  editJobTitle<T>(model: T, id: number) {
    console.log('model', model)
    console.log('j', id)

    return this._http.put<T>(
      `${environment.apiURL}usermanagement/editJobTitleforuser/${id}`,
      model,
    )
  }

  deleteUserDeptJobTitle(userid: number, id: number) {
    return this._http.delete(
      ` ${environment.apiURL}usermanagement/deleteUserDepartment/${userid}/${id}`,
    )
  }

  deleteJopTitle(id: number) {
    console.log(
      `${environment.apiURL}usermanagement/deleteJobTitleForuser/${id}`,
    )
    return this._http.delete(
      `${environment.apiURL}usermanagement/deleteJobTitleForuser/${id}`,
    )
  }
}
