import { TranslateService } from "@ngx-translate/core";
import { Component, ViewContainerRef, OnInit, OnDestroy } from "@angular/core";
import { Subscription } from "rxjs";
import { Router, NavigationEnd } from "@angular/router";
import { filter } from "rxjs/operators";
import { StyleService } from "./shared/services/StyleService.service";
import { AppStrings } from "./shared/data/app-strings";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
})
export class AppComponent implements OnInit, OnDestroy {
  subscription: Subscription;

  constructor(private router: Router, private appStrings: AppStrings) {}

  ngOnInit() {
    this.subscription = this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => window.scrollTo(0, 0));
    let currentLang = localStorage.getItem(this.appStrings.CURRENT_LANG);
    if (currentLang == null || currentLang == undefined) {
      localStorage.setItem(this.appStrings.CURRENT_LANG, this.appStrings.ar);
    }
  }



  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
